.signInScreen{
    max-width: 300px;
    padding: 70px;
    margin-left: auto;
    margin-right: auto;
    background: rgba(0, 0, 0, 0.85);
    /* display: flex; */
}
.signInScreen >form{
    display: grid;
    flex-direction: column;

}
.signInScreen >form>input{
    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 5px;
    border: none;
    padding: 5px 15px;

}
.signInScreen > form> button{
    padding: 16px 20px;
    font-size: 1rem;
    border: none;
    color: white;
    border-radius: 5px;
    background-color: #e50914;
    cursor: pointer;
    margin-top: 20px;
}
.grey{
    color: grey;
}
.link:hover{
    cursor: pointer;
    text-decoration: underline;
}