.nav{
    position: fixed;
    top: 0px;
    width: 100%;
    padding: 10px;
    height: 30px;
    z-index: 1;

    /* Animations */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav__contents{

    display: flex;
    justify-content: center;
}

.nav__logo{
    position: fixed;
    top: 10px;
    left: 0px;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;

}
.nav__black{
    background-color: #111;
}

.nav__avatar{
    position: fixed;
    cursor: pointer;
    right: 10px;
    width: 30px;

}